body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.header {
  min-height: 6rem;
}

.homepage {
  background-image: url("../img/homepageImg.png");
}

.homepagebtn {
  transition: all 0.3s;
  position: relative;
}
.homepagebtn span {
  position: absolute;
  top: -2px;
  left: 80%;
  width: 20px;
  height: 2px;
  background-color: black;
  transition: all 0.5s;
}
.homepagebtn span::before {
  content: "";
  position: absolute;
  top: 54px;
  right: 120px;
  width: 20px;
  height: 2px;
  background-color: black;
  transition: all 0.5s;
}

.homepagebtn:hover {
  border: 2px solid rgb(58, 216, 216);
  color: rgb(58, 216, 216);
  letter-spacing: 0.25em;
  box-shadow: 0 0 35px rgb(58, 216, 216);
}
.homepagebtn:hover span {
  position: absolute;
  top: -2px;
  left: 15%;
  width: 40px;
  height: 2px;
  background-color: black;
}
.homepagebtn:hover span::before {
  content: "";
  position: absolute;
  top: 54px;
  right: -100px;
  width: 40px;
  height: 2px;
  background-color: black;
}

.designerPage {
  background-image: url("../img/designerbg.png");
  background-size: cover;
  background-position: center;
  position: fixed;
  height: 100vh;
  width: 100%;
}

.designerBg {
  min-height: 75vh;
}

.services-animation {
  transition: 0.3s;
}

.services-animation:hover {
  transform: translateY(-10px);
  box-shadow: 0 0 0.5rem black;
}

.price-animation:hover {
  color: white;
  background-color: rgb(202, 138, 47);
  border: 2px solid rgb(202, 138, 47);
}

.aboutus-btn:hover {
  color: white;
}

.aboutus-btn-bg {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  background-color: rgb(146, 64, 14);
  transition: 0.5s;
  z-index: -1;
}

.aboutus-btn:hover .aboutus-btn-bg {
  width: 100%;
}

.reserveBg {
  background-image: url(../img/homepageImg.png);
  background-position: center;
  background-size: cover;
  position: relative;
}
.reserveBg .reserveBgFilter {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.9);
}

.member-bg {
  background-image: url("../img/homepageImg.png");
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100vh;
  position: relative;
}

.login-btn {
  transition: 0.3s;
}

.login-btn:hover {
  border: 2px solid rgb(105, 222, 233);
  color: rgb(105, 222, 233);
  box-shadow: 0 0 1.5rem rgb(105, 222, 233);
}

.footertitle::before {
  content: "";
  border-bottom: 2px solid rgb(202, 138, 47);
  height: 100%;
  width: 0;
  position: absolute;
  top: 1rem;
  left: 0;
  transition: 0.3s;
}

.footertitle:hover.footertitle::before {
  width: 100%;
}

.footerContent {
  transition: 0.3s;
}

.footerContent:hover {
  color: white;
  transform: translateY(-5px);
}

.footerImg {
  transition: 0.3s;
}

.footerImg:hover {
  background-color: white;
  transform: translateY(-5px);
}

.animation-moveInBottom {
  animation: moveInBottom 1s ease-out;
}

@media screen and (max-height: 1000px) {
  .homepage div {
    padding-top: 5rem !important;
  }
  .member-bg {
    min-height: 100vh;
  }
  .member-bg div {
    margin-top: 3rem !important;
  }
  .member-bg div section {
    gap: 1rem !important;
  }
}
@media screen and (max-width: 500px) {
  .homepage div {
    padding-top: 15rem !important;
  }
  .homepage div p {
    font-size: 1.5rem !important;
    margin-bottom: 1rem !important;
  }
  .homepage div h1 {
    font-size: 2.5rem !important;
  }
  .homepage div button {
    font-size: 1rem !important;
    margin-left: 6rem !important;
  }
  .footer-box {
    min-height: 100vh !important;
  }
  .services-box {
    padding-top: 10rem !important;
  }
  .services-box div {
    margin-top: 3rem !important;
  }
  .member-bg div {
    margin-top: 4.5rem !important;
  }
}
@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}/*# sourceMappingURL=style.css.map */